* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list__item {
  height: 50px;
  grid-gap: 10px;
  cursor: pointer;
  grid-template-rows: repeat(10, 1fr);
  grid-template-columns: 50px 900px;
  display: grid;
}

.country-list {
  width: 900px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list__name {
  margin: 0;
}

.country-list__item.isActiv-js .country-list__name {
  color: #3520ee;
  font-size: 30px;
}

.country-list__item.isActiv-js {
  border: 3px solid #64f170;
  border-radius: 10px;
}

.country-list__img {
  height: 100%;
  display: block;
}

.country-info__capital, .country-info__population, .country-info__languages {
  color: #212121;
  margin: 0;
  font-size: 25px;
  font-weight: 700;
}

.country-info__name {
  margin: 15px 0;
  font-size: 40px;
}

.country-info__flags {
  width: 200px;
